@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}

*:focus {
  outline: 0;
}

button {
  font-size: 16px;
  background: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

button:hover {
  background: #111;
}

.main-section {
  position: relative;
}

.main-section > a {
  position: fixed; 
  z-index: 1000; 
  top: 0px; 
  right: 0px; 
  border: 0px;
}

.chart-row {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  background: #f8f8f8;
}

.chart-row .description {
  min-width: 420px;
  max-width: 420px;
  box-sizing: border-box;
  padding: 20px;
  border-right: 1px solid #ccc;
}

.chart-row .description h3 {
  margin: 0 0 20px;
}

.live-chart {
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  position: relative;
  text-align: center; }
  .live-chart .start-button {
    margin: 0 auto; }
  .live-chart h1 {
    font-weight: 700;
    font-size: 60px;
    text-transform: uppercase;
    text-align: center;
    padding: 20px 10px;
    margin: 0; }
  .live-chart .chart {
    position: relative;
    margin: 20px auto; }
  .live-chart .baseline {
    position: absolute;
    top: 0;
    left: 50%;
    width: 2px;
    height: 100%;
    z-index: 2;
    background: #555; }
    .live-chart .baseline span {
      position: absolute;
      left: 0;
      top: -25px;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      font-style: italic; }
  .live-chart .chart-bars {
    position: relative;
    width: 100%; }
    .live-chart .chart-bars.with-baseline .bar-wrapper {
      left: 0;
      padding-left: 50%; }
      .live-chart .chart-bars.with-baseline .bar-wrapper label {
        right: 50%;
        width: auto; }
      .live-chart .chart-bars.with-baseline .bar-wrapper.behind-baseline {
        left: auto;
        right: 0;
        padding-left: 0;
        padding-right: 50%;
        flex-direction: row-reverse; }
        .live-chart .chart-bars.with-baseline .bar-wrapper.behind-baseline label {
          right: auto;
          left: 50%;
          text-align: left;
          flex-direction: row-reverse; }
          .live-chart .chart-bars.with-baseline .bar-wrapper.behind-baseline label img {
            margin: 0 10px 0 0; }
        .live-chart .chart-bars.with-baseline .bar-wrapper.behind-baseline .value {
          right: 100%;
          left: auto;
          margin-left: 0;
          margin-right: 10px; }
  .live-chart .bar-wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transition: top 0.5s linear;
    transition: top 0.5s linear;
    padding-left: 200px;
    box-sizing: border-box;
    width: 100%;
    justify-content: flex-start; }
    .live-chart .bar-wrapper label {
      position: absolute;
      height: 100%;
      width: 200px;
      left: 0;
      padding: 0 10px;
      box-sizing: border-box;
      text-align: right;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      font-size: 16px;
      font-weight: 700;
      display: flex;
      justify-content: flex-end;
      align-items: center; }
      .live-chart .bar-wrapper label img {
        max-height: 100%;
        max-width: 100%;
        margin-left: 10px; }
    .live-chart .bar-wrapper .value {
      font-size: 16px;
      font-weight: 700;
      margin-left: 10px; }
    .live-chart .bar-wrapper .bar {
      width: 0%;
      -webkit-transition: width 0.5s linear;
      transition: width 0.5s linear; }

